import React from 'react'
import { Container } from 'react-bootstrap'
import PropTypes from 'prop-types'
import './icon-description.scss'

const IconDescription = ({ title, desctiption, id }) => (
    <Container
        className={`icondescription icondescription__item-${id}`}
        data-aos="fade"
        data-aos-once="true"
    >
        <h3 className="icondescription_heading">{title}</h3>
        <p className="icondescription_text">{desctiption}</p>
    </Container>
)

IconDescription.propTypes = {
    title: PropTypes.string.isRequired,
    desctiption: PropTypes.string.isRequired,
    id: PropTypes.number.isRequired,
}

export default IconDescription
