import React, { useEffect } from 'react'
import { Link } from 'react-scroll'
import { Container, Nav } from 'react-bootstrap'
import PropTypes from 'prop-types'
import './sidebar.scss'
import { NAV } from '../header-content'

const Sidebar = ({ isSidebarOpen, windowHeight, isBottom, closeSidebar }) => {
    useEffect(() => {
        document.body.style.overflow = isSidebarOpen ? 'hidden' : 'unset'

        return () => {
            document.body.style.overflow = 'unset'
        }
    }, [isSidebarOpen])

    return (
        <Nav
            className={`col-8 d-block sidebar fixed-top ${
                isSidebarOpen ? 'sidebar-active' : null
            }`}
            style={{ height: `${windowHeight}px` }}
        >
            <div className="sidebar-sticky" />

            {NAV.ELEMENTS.map(({ NAME, LINK }, index) =>
                index === NAV.ELEMENTS.length - 1 ? (
                    <Link
                        className={`sidebar_nav-item ${
                            isBottom ? 'active' : undefined
                        }`}
                        activeClass="active"
                        to={LINK}
                        spy
                        smooth
                        duration={300}
                        key={NAME}
                        onClick={closeSidebar}
                    >
                        {NAME}
                    </Link>
                ) : (
                    <Link
                        className="sidebar_nav-item"
                        activeClass="active"
                        to={LINK}
                        spy
                        smooth
                        duration={300}
                        key={NAME}
                        onClick={closeSidebar}
                    >
                        {NAME}
                    </Link>
                )
            )}
        </Nav>
    )
}

Sidebar.propTypes = {
    isSidebarOpen: PropTypes.bool.isRequired,
    windowHeight: PropTypes.number.isRequired,
    isBottom: PropTypes.bool.isRequired,
    closeSidebar: PropTypes.func.isRequired,
}

export default Sidebar
