import React, { Component } from 'react'
import { Container } from 'react-bootstrap'
import { Link } from 'react-scroll'
import NavBar from './navbar/navbar'
import Heading from './heading/heading'
import Sidebar from './sidebar/sidebar'
import Backdrop from './backdrop/backdrop'
import { ReactComponent as ArrowDown } from '../../assets/arrow.svg'
import './header-section.scss'

class HeaderSection extends Component {
    constructor(props) {
        super(props)

        this.state = {
            isSidebarOpen: false,
            windowWidth: 0,
            windowHeight: 0,
            isBottom: false,
            isUnderHeader: false,
        }

        this.handleResize = (e) => {
            this.setState({
                windowWidth: window.innerWidth,
                windowHeight: window.innerHeight,
            })
        }

        this.handleScroll = () => {
            const { documentElement, body } = document
            const { innerHeight, pageYOffset } = window
            const windowHeight =
                'innerHeight' in window
                    ? innerHeight
                    : documentElement.offsetHeight

            const windowBottom = windowHeight + pageYOffset

            const docHeight = Math.max(
                body.scrollHeight,
                body.offsetHeight,
                documentElement.clientHeight,
                documentElement.scrollHeight,
                documentElement.offsetHeight
            )

            this.setState({
                isBottom: windowBottom >= docHeight,
                isUnderHeader: pageYOffset > windowHeight,
            })
        }

        this.openSidebar = () => {
            this.setState({ isSidebarOpen: true })
        }

        this.closeSidebar = () => {
            this.setState({ isSidebarOpen: false })
        }
    }

    componentDidMount() {
        window.addEventListener('resize', this.handleResize)
        window.addEventListener('scroll', this.handleScroll, { passive: true })
        this.handleResize()
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize)
        window.removeEventListener('scroll', this.handleScroll)
    }

    render() {
        const {
            isSidebarOpen,
            windowWidth,
            windowHeight,
            isBottom,
            isUnderHeader,
        } = this.state
        const { openSidebar, closeSidebar } = this

        return (
            <Container
                className="header_section"
                style={{ height: `${windowHeight}px` }}
                id="header"
            >
                <NavBar
                    openSidebar={openSidebar}
                    isBottom={isBottom}
                    windowWidth={windowWidth}
                />
                <Heading windowWidth={windowWidth} />
                <Sidebar
                    isSidebarOpen={isSidebarOpen}
                    windowHeight={windowHeight}
                    isBottom={isBottom}
                    closeSidebar={closeSidebar}
                />
                <Backdrop
                    isSidebarOpen={isSidebarOpen}
                    closeSidebar={closeSidebar}
                />
                <Link
                    className={`header_top-scroll ${
                        isUnderHeader && !isBottom ? 'active' : 'not-active'
                    }`}
                    to="header"
                    spy
                    smooth
                    duration={300}
                >
                    <ArrowDown />
                </Link>
            </Container>
        )
    }
}
export default HeaderSection
