import React from 'react';
import { Table } from 'react-bootstrap';
import { TIMING } from '../footer-content';
import './opening-hours.scss';

const OpeningHours = () => (
    <Table className="openinghours">
        <tbody>
            {TIMING.HOURS.map(({ DAY, HOUR }) => (
                <tr key={DAY}>
                    <td>{DAY}</td>
                    <td className="openinghours_time">{HOUR}</td>
                </tr>
            ))}
        </tbody>
    </Table>
);

export default OpeningHours;
