import React from "react";
import PropTypes from 'prop-types';
import "./footer-icon.scss";

const FooterIcon = ({ iconUrl, link, isPhone, isMail, text }) => {

    const mailOrLink = isMail ? `mailto:${link}` : link;
    const redirectLink = isPhone ? `tel:${link}` : mailOrLink;
    return (
        <a className="footericon_container" href={redirectLink}>
            <div className="footericon">
                <img className="footericon_img" src={iconUrl} alt="ikona kontaktowa"/>
            </div>
            <span className="footer_text">{text}</span>
        </a>
    );
};

FooterIcon.propTypes = {
    iconUrl: PropTypes.string.isRequired,
    link: PropTypes.string.isRequired,
    isPhone: PropTypes.bool,
    isMail: PropTypes.bool,
    text: PropTypes.string.isRequired,
};

FooterIcon.defaultProps = {
    isPhone: false,
    isMail: false,
};


export default FooterIcon;
