import React from "react";
import About from "./about/about";
import Booking from "./booking/booking";
import Price from "./price/price";
import Paralax from "../paralax/paralax";

import paralaxUrl1 from "../../assets/paralax_1.png";
import paralaxUrl2 from "../../assets/paralax_2.jpg";
import paralaxUrl3 from "../../assets/paralax_3.jpg";
import "./main.scss";

const Main = () => (
    <>
        <About />
        <Paralax imageUrl={paralaxUrl1} />
        <Booking />
        <Paralax imageUrl={paralaxUrl2} />
        <Price />
        <Paralax imageUrl={paralaxUrl3} />
    </>
);

export default Main;
