import React from 'react'
import { Container } from 'react-bootstrap'
import IconsGrid from './icons-grid/icons-grid'
import { ABOUT } from '../mainContent'
import './about.scss'

const About = () => (
    <Container className="about_container" id="about">
        <Container className="about">
            <h2 className="about_heading">{ABOUT.HEADING}</h2>
            {ABOUT.TEXTS.map((text) => (
                <p className="about_paragraph" key={text}>
                    {text}
                </p>
            ))}
            <IconsGrid />
        </Container>
    </Container>
)

export default About
