import React from 'react'
import { Navbar, Nav } from 'react-bootstrap'
import { Link } from 'react-scroll'
import PropTypes from 'prop-types'
import ToggleButton from '../toggle-button/toggle-button'
import { NAV } from '../header-content'
import './navbar.scss'

const MAX_WINDOW_WIDTH = 1024

const NavBar = ({ openSidebar, windowWidth, isBottom }) => (
    <>
        {windowWidth > MAX_WINDOW_WIDTH ? (
            <Navbar className="navbar">
                <Navbar.Brand className="mb-0 h1 navbar_nav-brand">
                    <Link to="header" spy smooth duration={300}>
                        {NAV.BRAND.NAME}
                    </Link>
                </Navbar.Brand>

                <Nav className="mr-auto">
                    {NAV.ELEMENTS.map(({ NAME, LINK }, index) =>
                        index === NAV.ELEMENTS.length - 1 ? (
                            <Link
                                className={`navbar_nav-link ${
                                    isBottom ? 'active' : undefined
                                }`}
                                activeClass="active"
                                to={LINK}
                                spy
                                smooth
                                duration={300}
                                key={NAME}
                                offset={-54}
                            >
                                {NAME}
                            </Link>
                        ) : (
                            <Link
                                className="navbar_nav-link"
                                activeClass="active"
                                to={LINK}
                                spy
                                smooth
                                duration={300}
                                key={NAME}
                                offset={-54}
                            >
                                {NAME}
                            </Link>
                        )
                    )}
                </Nav>
            </Navbar>
        ) : (
            <ToggleButton openSidebar={openSidebar} />
        )}
    </>
)

NavBar.propTypes = {
    openSidebar: PropTypes.func.isRequired,
    windowWidth: PropTypes.number.isRequired,
    isBottom: PropTypes.bool.isRequired,
}

export default NavBar
