import React from 'react'
import './gallery.scss'
import PropTypes from 'prop-types'
import GalleryCard from '../gallery-card/gallery-card'

import GalleryImage1 from '../../../assets/gallery/galleryImage1.png'
import GalleryImage2 from '../../../assets/gallery/galleryImage2.png'
import GalleryImage3 from '../../../assets/gallery/galleryImage3.png'
import GalleryImage4 from '../../../assets/gallery/galleryImage4.png'
import GalleryImage5 from '../../../assets/gallery/galleryImage5.jpg'
import GalleryImage6 from '../../../assets/gallery/galleryImage6.png'

const Gallery = ({ isPriceSection }) => {
    return isPriceSection ? (
        <section
            className="gallery gallery-pricing"
            id="gallery"
            data-aos="fade-right"
            data-aos-once="true"
        >
            <GalleryCard id={1} imgUrl={GalleryImage4} />
            <GalleryCard id={2} imgUrl={GalleryImage5} />
            <GalleryCard id={3} imgUrl={GalleryImage6} />
        </section>
    ) : (
        <section
            className="gallery"
            id="gallery"
            data-aos="fade-left"
            data-aos-once="true"
        >
            <GalleryCard id={1} imgUrl={GalleryImage1} />
            <GalleryCard id={2} imgUrl={GalleryImage2} />
            <GalleryCard id={3} imgUrl={GalleryImage3} />
        </section>
    )
}

Gallery.propTypes = {
    isPriceSection: PropTypes.bool.isRequired,
}

export default Gallery
