import React from 'react'
import { Container } from 'react-bootstrap'
import Gallery from '../gallery/gallery'
import priceUrl from '../../../assets/price.svg'
import './price.scss'

const Price = () => (
    <Container className="price_container" id="price">
        <h2 className="price_heading">Cennik</h2>

        <Container className="price" data-aos="fade-left" data-aos-once="true">
            <div className="price_content">
                <img src={priceUrl} className="price_img" alt="Cennik" />
            </div>

            <div className="price_gallery">
                <Gallery isPriceSection />
            </div>
        </Container>
    </Container>
)

export default Price
