import React, { Component } from 'react'
import { PropTypes } from 'prop-types'
import { Container } from 'react-bootstrap'
import { Link } from 'react-scroll'
import { HEADING } from '../header-content'
import { ReactComponent as ArrowDown } from '../../../assets/arrow.svg'
import './heading.scss'

const MAX_WINDOW_WIDTH = 1024

class Heading extends Component {
    constructor(props) {
        super(props)

        this.state = {
            isHeadingLoaded: false,
        }

        this.LOADING_TIME = 3600
    }

    componentDidMount() {
        this.timer = setTimeout(() => {
            this.setState({ isHeadingLoaded: true })
        }, this.LOADING_TIME)
    }

    componentWillUnmount() {
        clearTimeout(this.timer)
    }

    render() {
        const { isHeadingLoaded } = this.state
        const { windowWidth } = this.props

        const headingContent = isHeadingLoaded
            ? HEADING.PRIMARY_TEXT
            : HEADING.SECONDARY_TEXT

        return (
            <Container className="heading_container">
                <h1 className="heading_animated">{headingContent}</h1>
                <Link
                    className="heading_arrow"
                    to="about"
                    spy
                    smooth
                    duration={300}
                    offset={windowWidth >= MAX_WINDOW_WIDTH ? -54 : 0}
                >
                    <ArrowDown />
                </Link>
            </Container>
        )
    }
}

Heading.propTypes = {
    windowWidth: PropTypes.number.isRequired,
}

export default Heading
